import EventManager from "../../../eventManager/EventManager";
import { GameEvents } from "../../../gameEvents/GameEvents";
import { BaseState } from "../../../state-machine/BaseState";
import { GameStates } from "../StatesEnum";


export class PositioningState extends BaseState {

  begin(data?: any) {
    EventManager.dispatch(GameEvents.POSITION_START, data);
    EventManager.add(GameEvents.POSITION_END, this.end);
  }

  end = (): void => {
    this.stateMachine.setState(GameStates.BATTLE);
  }

  cleanUp(): void {

  }
}